// В этом файле должны быть стили для БЭМ-блока b-location, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.b-location {
  position: relative;

  @media (max-width: ($screen-md - 1)) {
    padding-top: 48px; // строка с кнопками Назад и Сохранить
  }

  &__inner {

    @media (min-width: $screen-md) {
      display: flex;
    }
  }

  &__search {
    display: none;

    @media (min-width: $screen-md) {
      display: block;
      padding-top: 32px;
      padding-bottom: 18px;
    }
  }

  &__top-wrap {
    position: relative;
    z-index: 99;
    background: #fff;
    transition: all 0.3s ease;

    @media (max-width: ($screen-md - 1)) {
      position: fixed;
      top: 96px;
      left: 0;
      right: 0;
      margin: 0;
      border-bottom: 1px solid #E8E8E8;
      transition: top 0.3s ease;

      .hide-header-search-xs & {
        top: -48px;
      }
    }
  }

  &__top {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    padding-top: 8px;
    padding-bottom: 8px;
  }

  &__content {
    position: relative;
  }

  &__sidebar {
    padding-top: 10px;
  }

  &__back {
    margin-left: -8px;
    margin-right: 12px;
    color: #D84B2A;
    font-size: 12px;
  }

  &__save {
    position: relative;
    margin-left: auto;
    margin-right: -12px;

    @media (min-width: $screen-md) {
      position: fixed;
      bottom: 20px;
      right: 40px;
    }
  }

  &__breadcrumbs {
    display: none;

    @media (min-width: $screen-md) {
      display: block;
    }
  }

  &__abuse-link {
    display: none;
    margin-left: auto;
    font-size: 12px;
    line-height: 16px;
    padding: 8px 0;
    color: #535353;
    text-decoration: underline;

    @media (min-width: $screen-md) {
      display: inline-block;
    }

    &:hover,
    &:focus {
      color: #535353;
      text-decoration: none;
    }
  }

  &__main-block {
    min-width: 100%;
    margin: 0 -15px;
    // padding-top: 6px;

    @media (min-width: $screen-md) {
      margin: 0;
      padding-top: 10px;
    }

    .big_img_slider {
      padding-bottom: 0;
    }

    .big_img_slider .jcarousel-control-next,
    .big_img_slider .jcarousel-control-prev {
      display: none;

      @media (min-width: $screen-xs) {
        display: block;
      }
    }
  }

  &__left-tabs {
    position: static;
    width: 100%;
    display: flex;
    align-items: center;
    margin: 12px 0 0;

    // @media (min-width: $screen-xl) {
    //   position: absolute;
    //   top: 10px;
    //   left: -43px;
    //   margin: 0;
    //   width: auto;
    //   flex-direction: column;
    // }

    li {
      margin: 0 12px 0 0;

      // @media (min-width: $screen-xl) {
      //   margin: 0 0 8px;
      // }
    }

    .report {
      margin-left: auto;

      @media (min-width: $screen-md) {
        display: none;
      }

      a {
        display: block;
        width: auto;
        height: auto;
        padding: 0;
        background: transparent;
        color: #535353;
        font-size: 12px;
        line-height: 1;
        text-decoration: underline;

        &:hover,
        &:focus {
          background: transparent;
          color: #535353;
          text-decoration: none;
        }
      }
    }
  }

  &__user-data-wrap {
    position: sticky;
    top: 60px;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
  }

  &__map-wrap {
    width: 100%;
    height: 468px;
    background: #ddd;
  }
}
