.flag-container {
  position: relative; }

.selected-flag {
  position: relative;
  outline: none; }
  .selected-flag .iti-flag {
    position: absolute;
    top: 1px;
    left: 1px;
    border-radius: 3px;
    padding: 0 0 0 14px;
    width: 40px;
    height: 40px !important;
    background: none;
    box-shadow: none;
    cursor: pointer; }
    .selected-flag .iti-flag:before {
      content: '';
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      margin: auto;
      box-shadow: 0 0 0 1px #f0f0f0; }
  .selected-flag .iti-arrow {
    position: absolute;
    top: 1px;
    right: 1px;
    width: 46px;
    height: 40px;
    border-radius: 3px;
    cursor: pointer; }
    .selected-flag .iti-arrow:before {
      content: '';
      position: absolute;
      top: 50%;
      margin-top: -2px;
      right: 18px;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 5px 5px 0 5px;
      border-color: #c4c4c4 transparent transparent transparent;
      transition: all 0.3s; }

